:root {
  --clr-1: #000000;
  --clr-2: #ffffff;

  --clr-3: #ff006f;
  --clr-4: #ca0058;
  --clr-5: #ff8dbe;

  --clr-p: #ffffffa6;

  --clr-bg: #ffffff10;
  --clr-border: #ffffff30;
  --clr-shadow: #ffffff50;
}

@font-face {
  font-family: DmSerif;
  src: url(./font/DMSerifDisplay-Regular.ttf);
}
@font-face {
  font-family: Oswald;
  src: url(./font/Oswald-Variable.ttf);
}
@font-face {
  font-family: Inconsolata;
  src: url(./font/Inconsolata-Variable.ttf);
}

h1,
h2,
h3,
h4,
button {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  text-shadow: 0 0 5px var(--clr-shadow);
}

h5,
p,
a,
li {
  font-family: "Inconsolata", sans-serif;
  text-transform: uppercase;
}

h1 {
  font-size: 6rem;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -2px;
}
h2 {
  font-size: 4rem;
  font-weight: 600;
  line-height: 1.1;
}
h3 {
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.1;
}
h4 {
  font-size: 1.2rem;
  font-weight: 400;
}
h5 {
  font-size: 1rem;
  font-weight: 600;

  position: relative;
  margin-bottom: 1rem;
  padding: 0 0 0 10px;
  color: var(--clr-p);
}
h5::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 5px;
  left: 0px;

  width: 5px;
  height: 5px;
  background-color: var(--clr-p);
  border-radius: 1rem;
  animation: h5blink 1s infinite linear;
}

p,
li,
a,
button {
  font-size: 1rem;
  font-weight: 400;
  color: var(--clr-p);
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
:root {
  overflow-x: hidden;
}
body {
  position: relative;

  font-size: 1rem;
  font-weight: 400;
  font-family: "Inconsolata", sans-serif;
  line-height: normal;
  letter-spacing: normal;
  color: var(--clr-2);
  background-color: var(--clr-1);

  overflow-x: hidden;
}

.section {
  width: 100%;
  padding: 10rem 5rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 1800px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro {
  width: 100%;
  max-width: 600px;
  margin-bottom: 5rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-wrap: balance;
}

button {
  padding: 1rem 2rem;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  color: var(--clr-2);
  border: none;
  border-radius: 20px;
  transition: 300ms;
}
button:hover {
  filter: brightness(50%);
  cursor: pointer;
}
button > img {
  height: 1rem;
}
.btn01 {
  color: var(--clr-1);
  background-color: transparent;
  border: solid 1px var(--clr-1);
}
.btn02 {
  color: var(--clr-2);
  background-color: transparent;
  box-shadow: 0 0 0 1px var(--clr-2) inset;
}
.btn02 img {
  filter: invert(1);
}
.btn03 {
  color: var(--clr-2);
  background-color: var(--clr-3);
  background: linear-gradient(180deg, var(--clr-3), var(--clr-4));
  border: none;
}
.btn03 img {
  filter: invert(1);
}
.btn05 {
  color: var(--clr-2);
  background-color: blue;
  background: linear-gradient(180deg, rgb(25, 25, 250), rgb(0, 0, 230));
  border: none;
}
.btn05 img {
  filter: invert(1);
}

::-webkit-scrollbar {
  width: 0.5rem;

  background: var(--clr-1);
}
::-webkit-scrollbar-track {
  border: none;
}
::-webkit-scrollbar-thumb {
  border: solid 2px var(--clr-1);
  border-radius: 1rem;

  background: var(--clr-2);

  cursor: grab;
}

/*-- responsive --*/

@media screen and (max-width: 768px) {
  h1 {
    font-size: 25vw;
  }
  h2 {
    font-size: 12vw;
  }
  h3 {
    font-size: 6vw;
  }

  .section {
    padding: 3rem 1rem;
  }
  .container {
    width: 100%;
  }
  .intro {
    width: 100%;
    margin-bottom: 2rem;
  }

  button {
    padding: 0.5rem 1rem;
  }

  .btnFlex {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
  }
  .btnFlex a,
  .btnFlex button {
    min-width: 220px;
  }
}
