.About01 {
  background-color: var(--clr-1);
}

.About01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.About01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.About01-box {
  width: 100%;
  max-width: 1000px;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5rem));
  gap: 10rem;
  justify-content: center;
  align-items: stretch;
}

.About01-text {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.About01-text > .buttons {
  margin-top: 1rem;

  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}

.About01-img {
  position: relative;
  z-index: 1;
  overflow: hidden;

  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  align-items: center;

  border-radius: 2rem;
}
.About01-img::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(200px);
}
.About01-img::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 20%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

.About01-img > img {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: cover;

  border-radius: 2rem;
}
.About01-img > div {
  width: 2rem;
  height: 2rem;

  background-color: var(--clr-bg);
  border-radius: 100%;
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .About01-box {
    grid-template-columns: repeat(2, calc(50% - 2.5rem));
    gap: 5rem;
  }
}

@media screen and (max-width: 768px) {
  .About01-box {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .About01-text {
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .About01-text > .buttons {
    flex-direction: column;
    width: 100%;
  }
  .About01-text > .buttons a {
    width: 100%;
  }
  .About01-text > .buttons button {
    width: 100%;
  }
}
