.Roadmap01 {
  padding-top: 0;
  background-color: var(--clr-1);
}
.Roadmap01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Roadmap01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Roadmap01-box {
  position: relative;
  z-index: 1;

  width: 100%;
  max-width: 1000px;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5rem));
  gap: 2rem 10rem;
  justify-content: center;
  align-items: stretch;
}
.vl {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  border-right: dotted 2px var(--clr-border);
}

.vl-fill {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  background-color: var(--clr-3);
  transition: height 0.1s ease-out;
}

.vl-head {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background-color: var(--clr-3);
  border-radius: 50%;
  z-index: 2;
  transition: top 0.1s ease-out;
}

.Roadmap01-card {
  position: relative;
  z-index: 1;
  overflow: hidden;

  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  border-radius: 2rem;
}

.Roadmap01-card:nth-child(2) {
  grid-row-start: 1;
  grid-row-end: 4;

  grid-column-start: 1;
  grid-column-end: 2;
}
.Roadmap01-card:nth-child(3) {
  grid-row-start: 3;
  grid-row-end: 6;

  grid-column-start: 2;
  grid-column-end: 3;
}
.Roadmap01-card:nth-child(4) {
  grid-row-start: 5;
  grid-row-end: 8;

  grid-column-start: 1;
  grid-column-end: 2;
}

.Roadmap01-card::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(200px);
}
.Roadmap01-card::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 20%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

.Roadmap01-card h4 {
  position: relative;
  z-index: 1;

  margin-bottom: 1rem;
  margin-left: 0.5rem;
  color: var(--clr-2);
}

.Roadmap01-card h4::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -0.1rem;
  left: -0.75rem;

  width: 2.1rem;
  height: 2.1rem;
  background-color: var(--clr-3);
  border-radius: 50%;
}

/*-- responsive --*/

@media screen and (max-width: 1200px) {
  .Roadmap01-box {
    grid-template-columns: repeat(2, calc(50% - 2.5rem));
    gap: 2rem 5rem;
  }
}

@media screen and (max-width: 768px) {
  .Roadmap01-box {
    padding-left: 3rem;
    grid-template-columns: 100%;
    gap: 2rem;
  }
  .Roadmap01-card:nth-child(2) {
    grid-row-start: 1;
    grid-row-end: 2;

    grid-column-start: 1;
    grid-column-end: 2;
  }
  .Roadmap01-card:nth-child(3) {
    grid-row-start: 2;
    grid-row-end: 3;

    grid-column-start: 1;
    grid-column-end: 2;
  }
  .Roadmap01-card:nth-child(4) {
    grid-row-start: 3;
    grid-row-end: 4;

    grid-column-start: 1;
    grid-column-end: 2;
  }

  .vl {
    left: 1rem;
  }
}
