.Navbar01 {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;

  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.Navbar01 .container {
  padding: 1rem;
}

.Navbar01-box {
  width: 100%;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.Navbar01-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;

  background-color: var(--clr-bg);
  border-radius: 1rem;
}
.Navbar01-links li {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.Navbar01-links li a {
  padding: 10px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;

  border-radius: 1rem;
}

.Navbar01-links li a.white {
  padding: 10px 15px;
  color: var(--clr-1);
  background-color: var(--clr-2);
}
.Navbar01-links li a:hover {
}

/*-- responsive --*/

@media screen and (max-width: 768px) {
  .Navbar01-links li:nth-child(4) {
    display: none;
  }
}
