.Footer01 {
  position: absolute;
  z-index: 1;
  bottom: 0;

  width: 100%;
  padding: 1rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 1rem;
}
.Footer01 p {
  color: var(--clr-2);
}

.Footer01-logo {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 1rem;
}
.Footer01-logo img {
  height: 3rem;
}

.Footer01-links {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  gap: 1rem;
}

/*-- responsive --*/

