.Token01 {
}

.ca {
  width: fit-content;
  padding: 0 0 0 1rem;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: start;
  align-items: center;

  text-wrap: nowrap;

  background-color: var(--clr-bg);
  border-radius: 1rem;
}
.Token01-box {
  position: relative;
  z-index: 1;

  width: 100%;
  max-width: 800px;
  margin-top: 4rem;

  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  gap: 0;
  justify-content: center;
  align-items: center;
}
.Token01-box > img {
  position: relative;
  z-index: 1;

  width: 100%;
  max-width: 300px;

  justify-self: center;
}
.Token01-box-left,
.Token01-box-right {
  position: relative;
  z-index: 2;

  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
}
.Token01-box-left {
  align-items: start;
}
.Token01-box-right {
  align-items: end;
}

.Token01-card {
  padding: 0.5rem 1rem;

  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: space-between;
  align-items: center;

  background-color: var(--clr-bg);
  border-radius: 1rem;
}

.Token01-box-blur {
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  height: 100%;
  aspect-ratio: 1/1;

  background-color: var(--clr-3);
  border-radius: 100%;

  filter: blur(100px);
}

.Token01-box::before,
.Token01-box::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  height: 120%;
  aspect-ratio: 1/1;

  border: dotted 5px var(--clr-border);
  border-radius: 100%;
}
.Token01-box::after {
  height: 150%;
  border: dotted 2px var(--clr-border);
}

/*-- responsive --*/

@media screen and (max-width: 768px) {
  .ca {
    width: 100%;
  }
  .ca h4 {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .Token01-box {
    margin-bottom: 3rem;
  }
  .Token01-box > img {
    width: 150%;
  }
}
