.Dex01 {
  padding: 0 5rem;
  background-color: var(--clr-1);
}

.Dex01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Dex01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.carousel-wrapper {
  overflow: hidden;
  width: 100%;
  margin-top: 2rem;
}

.carousel-track {
  display: flex;
  animation: scrollCarousel 60s linear infinite;
  animation-play-state: running;
}

.carousel-track:hover {
  animation-play-state: paused;
}

.carousel-item {
  height: 4rem;
  margin: 0 0.5rem;
  padding: 1rem 2rem;

  flex-shrink: 0; /* Prevents items from shrinking */
  text-align: center;

  border-radius: 1rem;
  transition: 300ms;
}
.carousel-item:hover {
  background-color: var(--clr-bg);
}

.carousel-item img {
  height: 2rem;
  filter: grayscale(100%);
}
.carousel-item p {
  display: none;
}

@keyframes scrollCarousel {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(
      calc(-1 * (160px + 1rem) * 8)
    ); /* (item-width + margin-x * 2) * number-of-original-items */
  }
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
}

@media screen and (max-width: 768px) {
  .Dex01 {
    padding: 0 1rem;
  }
  .carousel-item {
    padding: 1rem;
  }

  .carousel-item img {
  }
}
