.Slogan01 {
  position: relative;
  z-index: 1;
  overflow: hidden;

  padding: 5rem 0;
}
.Slogan01::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(200px);
}
.Slogan01::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 20%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

.Slogan01-box {
  width: 100%;

  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.Slogan01-slider {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  text-wrap: nowrap;

  animation: slogan 10s linear infinite;
}

@keyframes slogan {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.Slogan01-slider h2 {
}
.Slogan01-slider h2 span {
  color: var(--clr-5);
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
}

@media screen and (max-width: 768px) {
  .Slogan01 {
    padding: 3rem 1rem;
  }
}
