.Hero01 {
  position: relative;
  overflow: hidden;
  padding: 8rem 5rem 0;
}

.Hero01-box {
  position: relative;
  z-index: 1;

  width: 100%;
  padding: 5rem;
  display: grid;
  grid-template-columns: 20% 60% 20%;
  gap: 0;
  justify-content: center;
  align-items: end;

  border-radius: 3rem;

  overflow: hidden;
}
.Hero01-box::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(100px);
}
.Hero01-box::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 50%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

.Hero01-box > h1 {
}

.Hero01-box > img {
  justify-self: center;
  width: 100%;
  max-width: 500px;
  margin-bottom: -300px;
}
.Hero01-box > p {
  text-align: right;
}

/*-- responsive --*/
@media screen and (max-width: 1800px) {
}

@media screen and (max-width: 768px) {
  .Hero01 {
    height: auto;

    padding: 3rem 1rem 0;

    justify-content: center;
    align-items: center;
  }

  .Hero01-box {
    width: 100%;
    padding: 2rem;

    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    justify-content: center;
    align-items: stretch;
    text-align: center;

    border-radius: 2rem;
  }
  .Hero01-box > p {
    text-align: center;
  }
  .Hero01-box > img {
    grid-row-start: 3;
    grid-row-end: 4;
    width: 100%;
    max-width: 500px;
    margin-bottom: -50vw;
  }
}
