.How01 {
  padding-top: 0;
  background-color: var(--clr-1);
}
.How01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.How01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.How01-box {
  width: 100%;
  max-width: 1000px;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  justify-content: center;
  align-items: stretch;
}

.How01-box > div {
  position: relative;
  z-index: 1;
  overflow: hidden;

  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  border-radius: 2rem;
}
.How01-box > div:nth-child(1) {
  color: var(--clr-2);
  background-color: var(--clr-3);
  background: linear-gradient(180deg, var(--clr-3), var(--clr-4));
}

.How01-box > div::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(200px);
}
.How01-box > div::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 20%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

.How01-box > div h4 {
  position: relative;
  z-index: 1;

  margin-bottom: 2rem;
  margin-left: 0.5rem;
  color: var(--clr-2);
}
.How01-box > div:nth-child(1) h4 {
  color: var(--clr-3);
}

.How01-box > div h4::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -0.1rem;
  left: -0.75rem;

  width: 2.1rem;
  height: 2.1rem;
  background-color: var(--clr-3);
  border-radius: 50%;
}

.How01-box > div:nth-child(1) h4::before {
  color: var(--clr-3);
  background-color: var(--clr-2);
}

/*-- responsive --*/

@media screen and (max-width: 1800px) {
  .How01-box > div {
    padding: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .How01-box {
    grid-template-columns: 100%;
  }
  .How01-box > div {
    padding: 2rem 1rem;
  }
}
