.Join01 {
  padding: 0 1rem;
}

.Join01 p,
.Join01 h5 {
  color: var(--clr-2);
}

.Join01 h5::before {
  background-color: var(--clr-2);
}

.Join01-box {
  position: relative;
  width: 100%;
  max-width: 1000px;
  padding: 5rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  border-radius: 3rem 3rem 0 0;
  overflow: hidden;
}

.Join01-box > div.links {
  margin-top: 1rem;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.Join01-box > div.links a {
  transition: 300ms;
}
.Join01-box > div.links a:hover {
}
.Join01-box > div.links a img {
  width: 2.2rem;
  aspect-ratio: 1/1;
}

.Join01-box::before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 100%;
  height: 200px;

  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(200px);
}
.Join01-box::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(50%);

  width: 20%;
  height: 50px;

  background-color: var(--clr-2);
  border-radius: 50%;
  filter: blur(50px);
}

/*-- responsive --*/

@media screen and (max-width: 768px) {
  .Join01 {
    padding: 0;
  }
  .Join01-box {
    width: 100%;
    padding: 2rem 1rem;
  }

  .Join01-box h2 {
    font-size: 10vw;
  }

  .Join01-box > div.links a img {
    width: 2rem;
    aspect-ratio: 1/1;
  }
}
